body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  font-family: inherit;
  font-size: inherit;
}
.App {
  text-align: center;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-link {
  color: #61dafb;
}


input {
  border-radius: 6px;
}

.react-time-picker__wrapper {
  border: 0 !important
}

div.basic-time-container {
  border: 1px solid lightgray;
  border-radius: 3px;
  display: inline-block;
  width: 100%;
}




.break-picker {
    margin-top: 10px;
    margin-bottom: 10px;
}

.break-input {
    border: 0;
    width: 100%;
    text-align: center;
}


.break-output {
    margin-top: 5px;
}
.submit-button {
    width: 100%;
}

.submit-button-container {
    margin-top: 10px;
}
